		console.log('App Hide')
		//console.log(Vue);
	},
	//由于 onError 并不是完整意义的生命周期，所以只提供一个捕获错误的方法，在 app 的根组件上添加名为 onError 的回调函数即可
	 onError (err) {
	       console.error('ERP App onError...:'+err)
	   }
